  margin: 0 192px;
  padding: 0 32px;
}

.music-player-page {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

body,
table,
tr,
td,
th {
  color: white !important;
}

.width-size-100 {
