.album-cover {
  width: 100%;
  height: auto;
  aspect-ratio: 1 / 1;
  max-height: 300px;
  max-width: 300px;
  border-radius: 16px;
  object-fit: cover;
}

.playing-music-name {
  font-size: 24px;
  font-weight: bold;
}

.playing-music-artist {
  font-size: 16px;
  color: #e9e9e9;
}

.playing-music, .playing-music-info, .playing-status {
  max-width: 300px;
  width: 100%;
}

#musicEndTime{
  float: right;
}

@media screen and (max-width: 768px) {
  .playing-music,.playing-music-info, .playing-status {
    max-width: 100%;
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
